import { Component } from "react";
import Header from './Header';
import lang from "../language/lang";
class Guide extends Component{
    constructor(props){
        super(props);
        this.state={
            language:JSON.parse(localStorage.language).language
        }
    }

    render()
    {
        const{language}=this.state
        return(
            <>
              <main id="guide">
                    <header>
                        <h2>{lang[language].guide}</h2>
                    </header>
                    <section className={language==="jp"?"container-main":"container-main container-main-en"}>
                        <Header/>
                        <div className="container1">
                            <div className="crumb">
                                <p><a href="/dashboard/home">{lang[language].top} </a>&nbsp;&gt;&nbsp;{lang[language].guide}</p>
                            </div>
                            <div className="guide-container">
                                <div className="guide-card">
                                    <div>
                                        <h3>STEP1</h3>
                                        <h4>{lang[language].currentPoint}</h4>
                                        <img src={`/assets/img/point-${language}.png`} alt="" className="guide-sp" />
                                        <p>{lang[language].guidemessage1}</p>
                                    </div>
                                    <img src={`/assets/img/point-${language}.png`} alt="" className="guide-pc" />
                                </div>
                                <div className="guide-card">
                                    <div>
                                        <h3>STEP2</h3>
                                        <h4>{lang[language].dotPrice}</h4>
                                        <img src={`/assets/img/currency-${language}.png`} alt="" className="guide-sp" />
                                        <p>{lang[language].guidemessage2}</p>
                                    </div>
                                    <img src={`/assets/img/currency-${language}.png`} alt="" className="guide-pc" />
                                </div>
                            </div>
                        </div>
                    </section>
                </main>
            </>
        )
    }
}

export default Guide;